import React, {FunctionComponent} from 'react';
import ClassNames from './SplashPage.module.scss';
import {CmsPageContext} from "../../../../types/CmsPageContext";
import PageRoot from "../PageRoot/PageRoot";
import SplashSection, {AppearType, CmsProps as SplashSectionCmsProps} from "../../SplashSection/SplashSection";
import usePageItemsScroll from "./scrollHook";

interface Props {
    pageContext: {
        fields: {
            sections: SplashSectionCmsProps[];
        }
    }
}

const SplashPage: FunctionComponent<Props & CmsPageContext> =
    (props) => {
        const sections = props.pageContext.fields.sections;
        const {activeIndex, previousIndex} = usePageItemsScroll(sections.length);

        function renderComponents() {
            return sections.map((item, idx) => {
                const appearType = idx === activeIndex
                    ? previousIndex === undefined
                        ? AppearType.ACTIVE
                        : activeIndex > previousIndex
                            ? AppearType.ACTIVE_NEXT
                            : AppearType.ACTIVE_PREV
                    : idx === previousIndex
                        ? AppearType.WAS_ACTIVE
                        : AppearType.NONE;

                return (
                    <SplashSection key={item.sys.id}
                                   appearType={appearType}
                                   {...item}
                    />
                );
            });
        }

        return (
            <PageRoot {...props.pageContext.meta} withNavigation={false}>
                <div className={ClassNames.SplashPage}>
                    <div className={ClassNames.components}>
                        {renderComponents()}
                    </div>
                </div>
            </PageRoot>
        );
    };

export default SplashPage;
