/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, {FunctionComponent} from "react"
import Helmet from "react-helmet"

const AUTHOR = process.env.TWITTER_AUTHOR || '';
const TAB_TITLE_SUFFIX = process.env.TAB_TITLE_SUFFIX || '';

interface Props {
    description?: string;
    lang?: string;
    meta?: JSX.IntrinsicElements['meta'][];
    keywords?: string[];
    title: string;
}

const SEO: FunctionComponent<Props> = (
    {
        description = '',
        lang = 'en',
        meta = [],
        keywords = [],
        title
    }) => {

    const completeMeta = [
        {
            name: `description`,
            content: description,
        },
        {
            property: `og:title`,
            content: title,
        },
        {
            property: `og:description`,
            content: description,
        },
        {
            property: `og:type`,
            content: `website`,
        },
        {
            name: `twitter:card`,
            content: `summary`,
        },
        {
            name: `twitter:creator`,
            content: AUTHOR,
        },
        {
            name: `twitter:title`,
            content: title,
        },
        {
            name: `twitter:description`,
            content: description,
        },
    ]
        .concat(
            keywords.length > 0
                ? {
                    name: `keywords`,
                    content: keywords.join(`, `),
                }
                : []
        )
        .concat(meta as any);


    return (
        <Helmet
            htmlAttributes={{
                lang,
            }}
            title={title}
            titleTemplate={`%s${TAB_TITLE_SUFFIX}`}
            meta={completeMeta}
        />
    )
};


export default SEO;
