import React, {FunctionComponent} from 'react';
import classNames from 'classnames';
import ClassNames from './SplashSection.module.scss';
import SplitContainerClassNames from '../SplitContainer/SplitContainer.module.scss';
import RichText from "../content-types/RichText/RichText";

export interface CmsProps {
    sys: {
        id: string;
    }
    fields: {
        image: any;
        text: any;
        background1: string;
        background2: string;
    }
}

export enum AppearType {
    ACTIVE = 1,
    ACTIVE_NEXT,
    ACTIVE_PREV,
    WAS_ACTIVE,
    NONE,

}

interface Props {
    appearType: AppearType;
}

const SplashSection: FunctionComponent<Props & CmsProps> = (props) => {
    return (
        <div className={classNames(
            SplitContainerClassNames.SplitContainer,
            ClassNames.SplashSection,
            ClassNames[AppearType[props.appearType]]
        )}>
            <div className={classNames(SplitContainerClassNames.area1, ClassNames.area1)}
                 style={{background: props.fields.background1}}>
                <img className={ClassNames.img} src={props.fields.image.fields.file.url}/>
            </div>
            <div className={classNames(SplitContainerClassNames.area2, ClassNames.area2)}
                 style={{background: props.fields.background2}}>
                <RichText className={ClassNames.text} data={props.fields.text}/>
            </div>

        </div>
    );
};

export default SplashSection;
